"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import {
  ChevronRight,
  Clock,
  DollarSign,
  TrendingUp,
  Users,
  Calendar,
  Star,
  ArrowUpRight,
} from "lucide-react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Button } from "@/components/ui/button";
import { createClient } from "@/lib/supabase/client";

interface Session {
  id: string;
  title: string;
  seller: {
    name: string;
    avatar: string | null;
    verified: boolean;
  };
  date: string;
  time: string;
  duration: string;
  price: number;
  status: "upcoming" | "completed";
}

interface Recommended {
  id: string;
  title: string;
  seller: {
    name: string;
    avatar: string | null;
    rating: number;
  };
  price: number;
  duration: string;
  category: string;
}

interface Activity {
  id: string;
  type: "earning" | "booking";
  title: string;
  description: string;
  amount?: number;
  timestamp: string;
}

// Default empty data - replace with real API data
const defaultSessions: Session[] = [];
const defaultRecommended: Recommended[] = [];
const defaultActivity: Activity[] = [];

export function HomeTab() {
  const router = useRouter();
  const [userName, setUserName] = useState<string>("");
  const [totalEarnings] = useState(0);
  const [walletBalance] = useState(0);
  const [sessions] = useState(defaultSessions);
  const [recommended] = useState(defaultRecommended);
  const [activity] = useState(defaultActivity);

  // Fetch user data
  useEffect(() => {
    const supabase = createClient();
    supabase.auth.getUser().then(({ data: { user } }) => {
      if (user) {
        const name = user.user_metadata?.full_name || user.user_metadata?.name || user.email?.split("@")[0] || "User";
        setUserName(name);
      }
    });
  }, []);

  return (
    <div className="space-y-6 max-w-5xl">
      {/* Welcome Section */}
      <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4">
        <div>
          <h1 className="text-2xl font-bold tracking-tight">
            Welcome back{userName ? `, ${userName.split(" ")[0]}` : ""}! ðŸ‘‹
          </h1>
          <p className="text-muted-foreground text-sm mt-1">
            Here&apos;s what&apos;s happening with your sessions
          </p>
        </div>
      </div>

      {/* Quick Stats */}
      <div className="grid grid-cols-2 md:grid-cols-4 gap-3">
        <Card className="bg-card border-border">
          <CardContent className="p-4">
            <div className="flex items-center gap-3">
              <div className="w-10 h-10 rounded-lg bg-primary/10 flex items-center justify-center">
                <DollarSign className="w-5 h-5 text-primary" />
              </div>
              <div>
                <p className="text-xs text-muted-foreground font-medium">Total Earned</p>
                <p className="text-lg font-bold tracking-tight">${totalEarnings.toLocaleString()}</p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="bg-card border-border">
          <CardContent className="p-4">
            <div className="flex items-center gap-3">
              <div className="w-10 h-10 rounded-lg bg-primary/10 flex items-center justify-center">
                <TrendingUp className="w-5 h-5 text-primary" />
              </div>
              <div>
                <p className="text-xs text-muted-foreground font-medium">Balance</p>
                <p className="text-lg font-bold tracking-tight">${walletBalance.toLocaleString()}</p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="bg-card border-border">
          <CardContent className="p-4">
            <div className="flex items-center gap-3">
              <div className="w-10 h-10 rounded-lg bg-primary/10 flex items-center justify-center">
                <Calendar className="w-5 h-5 text-primary" />
              </div>
              <div>
                <p className="text-xs text-muted-foreground font-medium">Sessions</p>
                <p className="text-lg font-bold tracking-tight">0</p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="bg-card border-border">
          <CardContent className="p-4">
            <div className="flex items-center gap-3">
              <div className="w-10 h-10 rounded-lg bg-primary/10 flex items-center justify-center">
                <Users className="w-5 h-5 text-primary" />
              </div>
              <div>
                <p className="text-xs text-muted-foreground font-medium">Connections</p>
                <p className="text-lg font-bold tracking-tight">0</p>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Quick Actions */}
      <div className="grid grid-cols-1 sm:grid-cols-3 gap-2">
        <button
          onClick={() => router.push("/dashboard?tab=discover")}
          className="group flex items-center gap-3 p-3 rounded-lg bg-card border border-border hover:bg-muted/50 transition-all duration-200 text-left"
        >
          <div className="w-8 h-8 rounded-lg bg-muted flex items-center justify-center flex-shrink-0">
            <Users className="w-4 h-4 text-foreground" />
          </div>
          <div className="flex-1 min-w-0">
            <span className="text-xs font-semibold text-foreground tracking-tight block">
              Browse Sellers
            </span>
            <span className="text-[10px] text-muted-foreground block truncate">
              Find experts for your needs
            </span>
          </div>
          <ChevronRight className="w-3.5 h-3.5 text-muted-foreground flex-shrink-0 group-hover:translate-x-0.5 transition-all" />
        </button>

        <button
          onClick={() => router.push("/dashboard?tab=wallet")}
          className="group flex items-center gap-3 p-3 rounded-lg bg-card border border-border hover:bg-muted/50 transition-all duration-200 text-left"
        >
          <div className="w-8 h-8 rounded-lg bg-muted flex items-center justify-center flex-shrink-0">
            <DollarSign className="w-4 h-4 text-foreground" />
          </div>
          <div className="flex-1 min-w-0">
            <span className="text-xs font-semibold text-foreground tracking-tight block">
              Manage Wallet
            </span>
            <span className="text-[10px] text-muted-foreground block truncate">
              View balance and transactions
            </span>
          </div>
          <ChevronRight className="w-3.5 h-3.5 text-muted-foreground flex-shrink-0 group-hover:translate-x-0.5 transition-all" />
        </button>

        <button
          onClick={() => router.push("/dashboard?tab=profile")}
          className="group flex items-center gap-3 p-3 rounded-lg bg-card border border-border hover:bg-muted/50 transition-all duration-200 text-left"
        >
          <div className="w-8 h-8 rounded-lg bg-muted flex items-center justify-center flex-shrink-0">
            <Star className="w-4 h-4 text-foreground" />
          </div>
          <div className="flex-1 min-w-0">
            <span className="text-xs font-semibold text-foreground tracking-tight block">
              Your Profile
            </span>
            <span className="text-[10px] text-muted-foreground block truncate">
              Edit your public profile
            </span>
          </div>
          <ChevronRight className="w-3.5 h-3.5 text-muted-foreground flex-shrink-0 group-hover:translate-x-0.5 transition-all" />
        </button>
      </div>

      {/* Upcoming Sessions */}
      {sessions.filter((s) => s.status === "upcoming").length > 0 && (
        <div className="space-y-3">
          <div className="flex items-center justify-between">
            <h2 className="text-lg font-semibold tracking-tight">Upcoming Sessions</h2>
            <Button variant="ghost" size="sm" className="text-xs text-muted-foreground">
              View all
              <ArrowUpRight className="w-3 h-3 ml-1" />
            </Button>
          </div>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
            {sessions
              .filter((s) => s.status === "upcoming")
              .map((session) => (
                <Card
                  key={session.id}
                  className="group bg-card border-border hover:border-primary/30 transition-all cursor-pointer"
                >
                  <CardContent className="p-4">
                    <div className="flex items-start gap-3">
                      <Avatar className="w-10 h-10">
                        <AvatarImage src={session.seller.avatar || undefined} />
                        <AvatarFallback className="bg-muted text-muted-foreground text-xs">
                          {session.seller.name[0]}
                        </AvatarFallback>
                      </Avatar>
                      <div className="flex-1 min-w-0">
                        <div className="flex items-center gap-2">
                          <h3 className="text-sm font-semibold truncate">{session.title}</h3>
                          {session.seller.verified && (
                            <Badge variant="secondary" className="text-[10px] px-1.5 py-0">
                              Verified
                            </Badge>
                          )}
                        </div>
                        <p className="text-xs text-muted-foreground">with {session.seller.name}</p>
                        <div className="flex items-center gap-3 mt-2 text-xs text-muted-foreground">
                          <span className="flex items-center gap-1">
                            <Calendar className="w-3 h-3" />
                            {session.date}
                          </span>
                          <span className="flex items-center gap-1">
                            <Clock className="w-3 h-3" />
                            {session.time}
                          </span>
                        </div>
                      </div>
                      <div className="text-right">
                        <p className="text-sm font-bold text-primary">${session.price}</p>
                        <p className="text-[10px] text-muted-foreground">{session.duration}</p>
                      </div>
                    </div>
                  </CardContent>
                </Card>
              ))}
          </div>
        </div>
      )}

      {/* Recommended Sellers */}
      {recommended.length > 0 && (
        <div className="space-y-3">
          <div className="flex items-center justify-between">
            <h2 className="text-lg font-semibold tracking-tight">Recommended for You</h2>
            <Button
              variant="ghost"
              size="sm"
              className="text-xs text-muted-foreground"
              onClick={() => router.push("/dashboard?tab=discover")}
            >
              View all
              <ArrowUpRight className="w-3 h-3 ml-1" />
            </Button>
          </div>
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-3">
            {recommended.map((seller) => (
              <Card
                key={seller.id}
                className="group bg-card border-border hover:border-primary/30 transition-all cursor-pointer overflow-hidden"
              >
                <CardContent className="p-4">
                  <div className="flex items-center gap-3 mb-3">
                    <Avatar className="w-10 h-10">
                      <AvatarImage src={seller.seller.avatar || undefined} />
                      <AvatarFallback className="bg-muted text-muted-foreground text-xs">
                        {seller.seller.name[0]}
                      </AvatarFallback>
                    </Avatar>
                    <div className="flex-1 min-w-0">
                      <h3 className="text-sm font-semibold truncate">{seller.seller.name}</h3>
                      <div className="flex items-center gap-1">
                        <Star className="w-3 h-3 text-primary fill-primary" />
                        <span className="text-xs text-muted-foreground">{seller.seller.rating}</span>
                      </div>
                    </div>
                    <Badge variant="secondary" className="text-[10px]">
                      {seller.category}
                    </Badge>
                  </div>
                  <h4 className="text-sm font-medium mb-2 line-clamp-1">{seller.title}</h4>
                  <div className="flex items-center justify-between text-xs text-muted-foreground">
                    <span>{seller.duration}</span>
                    <span className="font-semibold text-foreground">${seller.price}</span>
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        </div>
      )}

      {/* Recent Activity */}
      {activity.length > 0 && (
        <div className="space-y-3">
          <h2 className="text-lg font-semibold tracking-tight">Recent Activity</h2>
          <Card className="bg-card border-border">
            <CardContent className="p-0">
              <div className="divide-y divide-border">
                {activity.map((item) => (
                  <div key={item.id} className="flex items-center gap-3 p-4">
                    <div
                      className={`w-8 h-8 rounded-lg flex items-center justify-center ${
                        item.type === "earning" ? "bg-success/10" : "bg-primary/10"
                      }`}
                    >
                      {item.type === "earning" ? (
                        <DollarSign
                          className={`w-4 h-4 ${
                            item.type === "earning" ? "text-success" : "text-primary"
                          }`}
                        />
                      ) : (
                        <Calendar className="w-4 h-4 text-primary" />
                      )}
                    </div>
                    <div className="flex-1 min-w-0">
                      <p className="text-sm font-medium">{item.title}</p>
                      <p className="text-xs text-muted-foreground">{item.description}</p>
                    </div>
                    <div className="text-right">
                      {item.amount && (
                        <p className="text-sm font-semibold text-success">+${item.amount}</p>
                      )}
                      <p className="text-[10px] text-muted-foreground">{item.timestamp}</p>
                    </div>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>
        </div>
      )}
    </div>
  );
}
