"use client";

import { useState, useEffect } from "react";
import {
  Edit2,
  Star,
  Calendar,
  DollarSign,
  Clock,
  Camera,
  Loader2,
} from "lucide-react";
import { Card, CardContent } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Label } from "@/components/ui/label";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogFooter,
} from "@/components/ui/dialog";

interface UserProfile {
  id: string;
  name: string | null;
  avatar: string | null;
  email: string | null;
  bio: string | null;
  createdAt: string;
  sellerProfile: {
    id: string;
    hourlyRate: number;
    bio: string | null;
    tagline: string | null;
    averageRating: number | null;
    totalSessionsCompleted: number;
    totalReviews: number;
    isVerified: boolean;
    isActive: boolean;
  } | null;
}

export function ProfileTab() {
  const [profile, setProfile] = useState<UserProfile | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [isEditDialogOpen, setIsEditDialogOpen] = useState(false);
  const [editForm, setEditForm] = useState({
    name: "",
    bio: "",
  });
  const [isSaving, setIsSaving] = useState(false);

  // Fetch user profile data
  useEffect(() => {
    async function fetchProfile() {
      try {
        setIsLoading(true);
        const response = await fetch("/api/app/profile");

        if (!response.ok) {
          if (response.status === 401) {
            setError("Please sign in to view your profile");
          } else {
            setError("Failed to load profile");
          }
          return;
        }

        const data = await response.json();
        setProfile(data.user);
        setEditForm({
          name: data.user.name || "",
          bio: data.user.bio || data.user.sellerProfile?.bio || "",
        });
      } catch (err) {
        console.error("Error fetching profile:", err);
        setError("Failed to load profile");
      } finally {
        setIsLoading(false);
      }
    }

    fetchProfile();
  }, []);

  const handleOpenEditDialog = () => {
    if (profile) {
      setEditForm({
        name: profile.name || "",
        bio: profile.bio || profile.sellerProfile?.bio || "",
      });
    }
    setIsEditDialogOpen(true);
  };

  const handleSaveProfile = async () => {
    if (!profile) return;

    setIsSaving(true);
    try {
      const response = await fetch("/api/app/profile", {
        method: "PATCH",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          name: editForm.name,
          bio: editForm.bio,
        }),
      });

      if (response.ok) {
        const data = await response.json();
        setProfile(data.user);
      }
    } catch (err) {
      console.error("Error saving profile:", err);
    } finally {
      setIsSaving(false);
      setIsEditDialogOpen(false);
    }
  };

  // Format date for display
  const formatMemberSince = (dateString: string) => {
    const date = new Date(dateString);
    return date.toLocaleDateString("en-US", { month: "long", year: "numeric" });
  };

  // Loading state
  if (isLoading) {
    return (
      <div className="flex items-center justify-center h-64">
        <Loader2 className="w-8 h-8 animate-spin text-muted-foreground" />
      </div>
    );
  }

  // Error state
  if (error || !profile) {
    return (
      <div className="flex flex-col items-center justify-center h-64 text-center">
        <p className="text-muted-foreground">{error || "Profile not found"}</p>
        <Button
          variant="outline"
          className="mt-4"
          onClick={() => window.location.reload()}
        >
          Try Again
        </Button>
      </div>
    );
  }

  const displayName = profile.name || "User";
  const displayBio = profile.bio || profile.sellerProfile?.bio || "";
  const displayTagline = profile.sellerProfile?.tagline || "";

  return (
    <div className="space-y-6 max-w-4xl">
      {/* Profile Header */}
      <Card className="bg-card border-border overflow-hidden">
        {/* Banner */}
        <div className="h-32 bg-gradient-to-r from-primary/20 via-primary/10 to-primary/5" />

        <CardContent className="relative pt-0 pb-6">
          {/* Avatar */}
          <div className="absolute -top-12 left-6">
            <Avatar className="w-24 h-24 border-4 border-background">
              <AvatarImage src={profile.avatar || undefined} />
              <AvatarFallback className="bg-muted text-muted-foreground text-2xl">
                {displayName[0]}
              </AvatarFallback>
            </Avatar>
          </div>

          {/* Edit Button */}
          <div className="flex justify-end pt-2">
            <Button
              variant="outline"
              size="sm"
              className="gap-2"
              onClick={handleOpenEditDialog}
            >
              <Edit2 className="w-3 h-3" />
              Edit Profile
            </Button>
          </div>

          {/* Profile Info */}
          <div className="mt-4 space-y-3">
            <div>
              <h1 className="text-xl font-bold tracking-tight">{displayName}</h1>
              {profile.email && (
                <p className="text-sm text-muted-foreground">{profile.email}</p>
              )}
            </div>

            {displayTagline && (
              <p className="text-sm font-medium text-foreground">{displayTagline}</p>
            )}

            {displayBio && (
              <p className="text-sm text-muted-foreground">{displayBio}</p>
            )}

            <div className="flex flex-wrap gap-4 text-xs text-muted-foreground">
              <span className="flex items-center gap-1">
                <Calendar className="w-3 h-3" />
                Joined {formatMemberSince(profile.createdAt)}
              </span>
            </div>

            {/* Seller Badges */}
            {profile.sellerProfile && (
              <div className="flex flex-wrap gap-2 pt-2">
                {profile.sellerProfile.isVerified && (
                  <Badge variant="secondary" className="gap-1">
                    <span>âœ“</span>
                    Verified Seller
                  </Badge>
                )}
                {profile.sellerProfile.isActive && (
                  <Badge variant="secondary" className="gap-1">
                    <span>ðŸŸ¢</span>
                    Active
                  </Badge>
                )}
                {profile.sellerProfile.totalSessionsCompleted > 0 && (
                  <Badge variant="secondary" className="gap-1">
                    <span>â­</span>
                    {profile.sellerProfile.totalSessionsCompleted} Sessions
                  </Badge>
                )}
              </div>
            )}
          </div>
        </CardContent>
      </Card>

      {/* Seller Stats */}
      {profile.sellerProfile && (
        <div className="grid grid-cols-2 md:grid-cols-4 gap-3">
          <Card className="bg-card border-border">
            <CardContent className="p-4 text-center">
              <div className="w-10 h-10 rounded-lg bg-primary/10 flex items-center justify-center mx-auto mb-2">
                <Calendar className="w-5 h-5 text-primary" />
              </div>
              <p className="text-2xl font-bold tracking-tight">
                {profile.sellerProfile.totalSessionsCompleted}
              </p>
              <p className="text-xs text-muted-foreground">Sessions Completed</p>
            </CardContent>
          </Card>

          <Card className="bg-card border-border">
            <CardContent className="p-4 text-center">
              <div className="w-10 h-10 rounded-lg bg-primary/10 flex items-center justify-center mx-auto mb-2">
                <DollarSign className="w-5 h-5 text-primary" />
              </div>
              <p className="text-2xl font-bold tracking-tight">
                ${profile.sellerProfile.hourlyRate}
              </p>
              <p className="text-xs text-muted-foreground">Hourly Rate</p>
            </CardContent>
          </Card>

          <Card className="bg-card border-border">
            <CardContent className="p-4 text-center">
              <div className="w-10 h-10 rounded-lg bg-primary/10 flex items-center justify-center mx-auto mb-2">
                <Star className="w-5 h-5 text-primary" />
              </div>
              <p className="text-2xl font-bold tracking-tight">
                {profile.sellerProfile.averageRating?.toFixed(1) || "N/A"}
              </p>
              <p className="text-xs text-muted-foreground">Average Rating</p>
            </CardContent>
          </Card>

          <Card className="bg-card border-border">
            <CardContent className="p-4 text-center">
              <div className="w-10 h-10 rounded-lg bg-primary/10 flex items-center justify-center mx-auto mb-2">
                <Clock className="w-5 h-5 text-primary" />
              </div>
              <p className="text-2xl font-bold tracking-tight">
                {profile.sellerProfile.totalReviews}
              </p>
              <p className="text-xs text-muted-foreground">Total Reviews</p>
            </CardContent>
          </Card>
        </div>
      )}

      {/* Edit Profile Dialog */}
      <Dialog open={isEditDialogOpen} onOpenChange={setIsEditDialogOpen}>
        <DialogContent className="sm:max-w-[500px] bg-background border-border">
          <DialogHeader>
            <DialogTitle>Edit Profile</DialogTitle>
          </DialogHeader>

          <div className="space-y-4 py-4">
            {/* Avatar Upload */}
            <div className="flex items-center gap-4">
              <div className="relative">
                <Avatar className="w-16 h-16">
                  <AvatarImage src={profile.avatar || undefined} />
                  <AvatarFallback className="bg-muted text-muted-foreground text-xl">
                    {editForm.name[0] || "U"}
                  </AvatarFallback>
                </Avatar>
                <button className="absolute -bottom-1 -right-1 w-6 h-6 rounded-full bg-primary flex items-center justify-center text-primary-foreground hover:bg-primary/90 transition-colors">
                  <Camera className="w-3 h-3" />
                </button>
              </div>
              <div className="flex-1">
                <p className="text-sm font-medium">Profile Photo</p>
                <p className="text-xs text-muted-foreground">Photo is synced from your account</p>
              </div>
            </div>

            {/* Name */}
            <div className="space-y-2">
              <Label htmlFor="name">Name</Label>
              <Input
                id="name"
                value={editForm.name}
                onChange={(e) => setEditForm((prev) => ({ ...prev, name: e.target.value }))}
                placeholder="Your name"
              />
            </div>

            {/* Bio */}
            <div className="space-y-2">
              <Label htmlFor="bio">Bio</Label>
              <Textarea
                id="bio"
                value={editForm.bio}
                onChange={(e) => setEditForm((prev) => ({ ...prev, bio: e.target.value }))}
                placeholder="Tell us about yourself"
                rows={3}
              />
            </div>
          </div>

          <DialogFooter>
            <Button
              variant="outline"
              onClick={() => setIsEditDialogOpen(false)}
              disabled={isSaving}
            >
              Cancel
            </Button>
            <Button onClick={handleSaveProfile} disabled={isSaving}>
              {isSaving ? (
                <>
                  <Loader2 className="w-4 h-4 mr-2 animate-spin" />
                  Saving...
                </>
              ) : (
                "Save Changes"
              )}
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </div>
  );
}
